<template>
    <div class="header">
        <div class="menu">
            <x-icon name="menu" class="icon" @click="onClickMenu"></x-icon>
        </div>
        <div class="logo" @click="link('index')">
            <span class="icon">
                <x-icon name="snow" style="width:100%;height:100%;color:#1890ff;"></x-icon>
            </span>
            <h1 class="title"><span>X</span>ue - ui</h1>
        </div>
        <ul class="navbar">
            <li @click="link('github')">
                <x-icon name="github" style="margin-right:3px;color:rgba(0,0,0,0.85)"></x-icon>
                Github
            </li>
            <li @click="link('react')">
                Xue-react
            </li>
        </ul>
    </div>
</template>
<script>
    export default {
        name: 'Header',
        inject: ['eventBus'],
        mounted() {},
        methods: {
            link(type) {
                type === 'index' && this.$router.push('/')
                type === 'github' &&
                    window.open('https://github.com/BlameDeng/Xue-ui', '_blank')
                type === 'react' && window.open('https://github.com/BlameDeng/Xue-react', '_blank')
            },
            onClickMenu() {
                this.eventBus.$emit('click-menu')
            }
        }
    }
</script>
